<template>
    <div>
        <v-navigation-drawer
            v-model="drawer"
            app
            :mini-variant.sync="mini"
            mini-variant-width="90"
            permanent
            color="#171819"
        >
          <v-navigation-drawer
              v-model="drawer"
              app
              :mini-variant.sync="mini"
              mini-variant-width="90"
              permanent
              color="#171819"
          >
            <v-list-item class="px-2">
              <v-list-item-avatar>
                <v-img src="https://randomuser.me/api/portraits/men/85.jpg"></v-img>
              </v-list-item-avatar>

              <v-list-item-title>Петр Иванов</v-list-item-title>

              <v-btn
                  icon
                  @click.stop="mini = !mini"
              >
                <v-icon>mdi-chevron-left</v-icon>
              </v-btn>
            </v-list-item>
            <v-list dense>
              <v-list-item
                  v-for="item in items"
                  :key="item.title"
                  :to="item.link"
                  link
              >
                <v-list-item-icon>
                  <v-icon>{{ item.icon }}</v-icon>
                </v-list-item-icon>

                <v-list-item-content>
                  <v-list-item-title>{{ item.title }}</v-list-item-title>
                </v-list-item-content>
              </v-list-item>
            </v-list>

            <v-fab-transition>
              <v-btn
                  key="1"
                  color="#0496FF"
                  fixed
                  fab
                  bottom
                  left
                  to="/createtask"
              >
                <v-icon>mdi-plus</v-icon>
              </v-btn>
            </v-fab-transition>
          </v-navigation-drawer>
        </v-navigation-drawer>

        <v-navigation-drawer
                app
                clipped
                right
                width="350"
                color="#1A1E24"
        >
            <v-subheader>
                Комментарии к задаче
            </v-subheader>
            <v-timeline dense style="padding-right: 10px">
                <v-slide-x-reverse-transition
                        group
                        hide-on-leave
                >
                    <v-timeline-item
                            v-for="item in commentsTask"
                            :key="item.id"
                            small
                            fill-dot
                    >
                        <v-alert
                                :value="true"
                                color="blue lighteen-1"
                                class="white--text"
                        >
                            <p>{{item.message}}</p>
                            <v-subheader>
                                {{item.user.name}} / {{item.user.housing}}
                            </v-subheader>
                        </v-alert>
                    </v-timeline-item>
                </v-slide-x-reverse-transition>
            </v-timeline>
        </v-navigation-drawer>

        <v-main>
            <create-task />
        </v-main>

        <v-footer
                app
                inset
        >
        </v-footer>
    </div>
</template>

<script>
    import {mapActions, mapState} from 'vuex'
    import CreateTask from "../components/Task/CreateTask";
    export default {
        name: "NewTasks",
        components: {CreateTask},
      data(){
          return{
            items: [
              { title: 'Глвная', link: '#/', icon: 'mdi-home-city' },
              { title: 'Аккаунт', link: '#/account', icon: 'mdi-account' },
              { title: 'Задачи', link: '/tasks', icon: 'mdi-calendar-check-outline' },
              { title: 'Настройки', link: '#/settings', icon: 'mdi-cog-outline' },
              { title: 'Выход', link: '/', icon: 'mdi-logout' },
            ],
            drawer: false,
            mini: true,
          }
      },
        computed: {
            ...mapState('tasks', {
                commentsTask: 'commentsTask'
            })
        },
        methods: {
            ...mapActions('tasks', {
                getCommentsTask: 'get_comments_task'
            })
        },
        created() {
            this.getCommentsTask(this.$root.$route.params.idtask)
        }
    }
</script>

