              { title: 'Аккаунт', link: '#/account', icon: 'mdi-account' },
              { title: 'Задачи', link: '/tasks', icon: 'mdi-calendar-check-outline' },
              { title: 'Настройки', link: '#/settings', icon: 'mdi-cog-outline' },
              { title: 'Выход', link: '/', icon: 'mdi-logout' },
            ],
            drawer: false,
            mini: true,
          }
        }
    }
</script>

<style scoped>
  .tasks{
    display: grid;
    grid-template-columns: 100px 3fr 1fr;
  }
  .navigation{
    grid-area: 1 / 1 / span 1 / span 1;
  }
  .tasks-manager{
    grid-area: 1 / 2 / span 1 / span 1;
  }
  .time-line{
    grid-area: 1 / 3 / span 1 / span 1;
  }
</style>
